.half {
  main {
    display: flex;
    flex-direction: column;

    @include min(md) {
      flex-direction: row;
      height: 100vh;
      width: 100vw;
      align-items: stretch;
    }

    > div {
      padding: 20px;

      @include min(md) {
        flex: 1;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        padding: $padding;
      }
    }

    .half-link {
      background-image: url("/assets/img/hilite.png");
      background-size: contain;
      filter: hue-rotate(30deg) saturate(30%);
      text-decoration: none;
      padding-top: 5px;
      padding-bottom: 3px;
      color: black;
      font-weight: 500;

      &:hover, &:active {
        text-decoration: underline;
        filter: hue-rotate(20deg) saturate(70%);
      }
    }

    .link-icon {
      width: 20px;

      & span {
        width: 8px;
        height: 8px;
        color: #5c6c70;
      }
      
      &:hover {
        color: #ffd966;
      }
    }
  }

  .social-bottom {
    position: relative;
    margin-top: 80vh;
    width: 100%;
    text-align: center;
  }

  &__content {
    order: 2;

    &.half--left {
      @include min(md) {
        order: 1;
        flex-basis: 0.75;
        max-width: 40vw;
      }
    }
  }

  &__image {
    min-height: 50vh;
    order: 1;
    background-size: cover;
    background-position: center;
    @include responsivebackground("half-sm.jpg", "half-md.jpg", "half-lg.jpg");
  }
}
