@keyframes ext-animation {
  from {color: #888;}
  to {color: $hover-green;}
}

figure {
  position: relative;
  width: 80%;
  display: block;
  margin: 40px auto;


  figcaption {
    text-align: center;

    a, p {
      text-decoration: none;
      display: inline-block;
      font-style: italic;
      color: #aaa;
      margin-top: 0;
    }

    div {
      font-size: 16px;
      margin-left: 5px;
      display: inline-block;

      span {
        color: #888;
      }
    }

    &:hover, &:active {
      a {
        text-decoration: underline;
        color: $dark-pink;
      }

      span {
        color: $hover-green;
        animation-name: ext-animation;
        animation-duration: 1s;
        animation-fill-mode: forwards;
      }
        
    }
  }
}